_c('v-img',{attrs:{"max-width":"50%","src":"http://agilerussia.ru/wp-content/uploads/2012/12/Scaling_Agile_61.jpg"}})],1),_c('h3',[_vm._v("Стимулирование команды")]),_c('p',[_vm._v(" Чтобы стимулировать обучение и инновации, отряды поощряют к тому, чтобы примерно 10% времени отводилось на «хакерские дни». Во время «хакерских дней» люди делают что им хочется, обычно они проверяют новые идеи и обмен мнениями со своими товарищами. У некоторых команд – один хакерский день каждую вторую неделю, другие собирают свои дни в «хакерскую неделю». "),_c('br'),_vm._v(" Хакерские дни – это не только прекрасное времяпровождение, это также отличный способ узнавать о современных инструментах и практиках, а иногда это ведет к важным инновациям в продукте! "),_c('br'),_vm._v(" У отряда нет формально назначенного лидера, но есть product owner. Product owner отвечает за приоритезацию работ команды, но он не вовлекается в то, как выполняется сама работа. Product owner-ы разных отрядов взаимодействуют друг с другом, чтобы поддерживать высокоуровневый документ, который показывает, куда же движется Spotify как единое целое. И каждый product owner отвечает за поддержку баклога продукта для своего отряда. "),_c('br'),_vm._v(" У каждой команды есть Agile Coach. ")]),_c('v-divider',{staticClass:"ma-2"}),_c('h2',[_vm._v("Кланы")]),_c('p',[_c('strong',[_vm._v("Клан (Tribe) – это набор отрядов , которые работают во взаимосвязанных областях, таких как плеер или инфраструктура бэкэнда.")]),_c('br'),_vm._v(" Клан можно представить как “инкубатор” для мини-стартапов-отрядов, у них большая степень свободы и автономии. У каждого клана есть лидер, который отвечает за создание наилучших условий существования отрядов внутри клана. Отряды клана физически находятся в одном офисе, рядом друг с другом, и расположенные рядом гостиные способствуют общению между отрядами. ")]),_c('v-col',{staticClass:"d-flex justify-center",attrs:{"cols":"12"}},[_c('v-img',{attrs:{"max-width":"50%","src":"http://agilerussia.ru/wp